import { useState, useEffect } from "react";
import firebase from "...../../firebase";
import { Link } from "react-router-dom";
import { MovieType } from "src/modules/user/list-film";

export function ListFilm() {
  const [movies, setMovies] = useState<MovieType[]>([]);
  const [loading, setLoading] = useState(false);

  const ref = firebase.firestore().collection("movies");

  function getMovies() {
    setLoading(true);
    ref.onSnapshot((querySnapshot) => {
      const items: MovieType[] = [];
      querySnapshot.forEach((doc) => {
        items.push(doc.data() as MovieType);
      });
      setMovies(items);
      setLoading(false);
    });
  }

  useEffect(() => {
    getMovies();
    document.title = "Home Page";
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  if (loading) {
    return <h1 className="text-white text-xl">Loading... </h1>;
  }

  return (
    <div className="flex flex-wrap">
      {movies.map((movie) => (
        <div className="flex justify-evenly m-auto ml-8 mt-12">
          <div>
            <Link to={`/home/${movie.slug}`}>
              <img
                className="rounded-lg max-w-56 h-52 hover:scale-105 duration-300 ease-linear"
                src={movie.image}
                alt={movie.name}
              />
              <div className="flex flex-wrap mt-6">
                <img
                  src="https://www.fullphimz.net/static/5fe2d564b3fa64a886a11cee/5fe2d564b3fa647446a11d19_tag.svg"
                  className="h-4 w-4 mr-3 mt-1"
                  alt={movie.name}
                />
                <a className="text-white underline text-gray-400 hover:text-white mr-1">
                  {movie.category}
                </a>
              </div>
              <p className="text-white w-40 font-bold text-xl capitalize hover:underline">
                {movie.name}
              </p>
            </Link>
          </div>
        </div>
      ))}
    </div>
  );
}
